.App {
  text-align: center;
}

img.center {
  display: block;
  margin: 0 auto;
}

footer{
  margin-bottom: -100px;
  margin-top: 20px;
  display: flex;
  align-items: flex-end;
}
#body_main{
  margin-bottom: -125px;
  margin-top: -20px;
}
#misis{
  width: 100px;
  margin-left: 25px;
  margin-bottom: 25px;
}

.center-in {
  display: table;
  margin: 0 auto;
  width: 100%;
  justify-content: center;
}

.main{ background-color: #ddd;}
.left {
  border:1px solid black;
    display: table-cell;
    margin: 0 auto;
}
.middle {

    display: table-cell;
}
.right {

    display: table-cell;
}

.item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 250px;
  width: 100%;
  color: #fff;
  background-color: #00008B;
  margin: 0 15px;
  font-size: 200%;
}

.pict-box {
  height: 100px;
  width: 100px;
  border: 4px black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
